<template>
    <div class="container">
        <div class="main__posts" v-if="posts">
            <div class="main__posts-item" v-for="post in posts" key:product.id>
                <p>Id: {{ post.id }}</p>
                <p>Title: {{ post.title }}</p>
                <p>Body: {{ post.body }}</p>
            </div>           
        </div> 
        <div class="main__section" v-else> Waiting......</div>
    </div>
</template>

<script>
    export default {
        data(){
        return {
                posts: null
            }
        },
        created() {
            this.getPosts()
        },
        methods:{
            async getPosts(){
            let response = await fetch ('https://jsonplaceholder.typicode.com/posts')
            let data = await response.json()
            this.posts = data
            }
        }
    }
</script>

